.aboutContainer{
 display: flex;
 background-color: var(--center-color);
 color: black;
 height: 100vh;
 padding: 6.25rem 0 6.25rem 0;
 overflow: hidden;
}

.aboutDesc{
    display: flex;
    flex-direction: column;
    justify-content: center;
    width: 50%;
    padding: 0 6.25rem 0 6.25rem;
}

.aboutDesc > h3{
    margin-bottom: 1.875rem;
    font-size: 1.75rem;

}


.aboutDesc > p{
    font-size: 1.45rem;
    line-height: 1.5;
}



.aboutImg {
    width: 45%;
    height: 37.5rem;
    display: flex;
    align-self: center;
}

.imageBottom{
    transition: opacity 3s ease-out;
    opacity: 0;
    height: 0;
    overflow: hidden;
}
.imageTop{
    transition: opacity 3s ease-out;
}

.aboutImg:hover .imageTop{
    opacity: 0;
    height: 0;
    overflow: hidden;
}

.aboutImg:hover .imageBottom{
    opacity: 1;
    height: auto;
}



.separator
{
    position: relative;
    margin-top: 1px;        
    height: 1px;
}

.separatorTransparent:before
{
    content: "";
    position: absolute;
    top: 0;
    left: 5%;
    right: 5%;
    width: 90%;
    height: 1px;
    background-image: linear-gradient(to right, transparent, rgb(48,49,51), transparent);
}